.x_size {
  max-width: 1000px;
  margin: auto;
  min-width: 330px;
}

#container {
  margin-top: 10px;
  width: 1000px;
}

#contents, section {
  width: 100%;
  color: black;
}

.content {
  width: 100%;
}

@media screen and (min-width: 419px) and (max-width: 800px) {

  #container {
    margin-top: 90px;
  }
  #contents, section {
    width: 100%;
    float: none;
  }
}

@media screen and (max-width: 418px) {

  #container {
    margin-top: 90px;
  }
  div .fb-like-box {
    display: none
  }
  #contents, section {
    width: 100%;
    float: none;
  }
}

